.nav{
    grid-area: nav;
    background-color: #2f3136;
  }

  .nav a:hover, a.active{
    background-color: #36393f;
    border-radius: 10px;
    margin-left: 5px;
    margin-right: 5px;
  }
  .nav a{
    color: #ffffff;
    text-decoration: none;
    font-size: 18px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;
    padding: 5px 0;
  }
.nav a:first-child{
  margin-top: 50px;
}
.nav a:last-child{
  margin-bottom: 50px;
}