body{
  background-color: #212326;
  color: #ffffff;
}
.app-wrapper{
  margin: 0 auto;
  max-width: 1200px;
  gap: 10px;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas: "header header" "nav main-content";
}

.app-wrapper-content{
    grid-area: main-content;
  }